.c-orderList {
  overflow: hidden;
  width: 100%;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
.c-orderList-empty {
  margin-top: 2.5rem;
  text-align: center;
  padding: 1.25rem;
  color: #868686;
  min-height: 25rem;
}
.c-orderList-down-text {
  width: 100%;
  height: 2.8125rem;
  padding: 1.5625rem 0 1.25rem;
  text-align: center;
  color: #aaa;
}
.c-orderItem {
  display: flex;
  justify-content: space-between;
  margin: 1rem 0.625rem;
  padding: 1rem 0.875rem;
  background: #fff;
  border-radius: 0.125rem;
  box-shadow: #e5e5e5 0 0 0.625rem;
}
.c-orderItem:last-child {
  margin-bottom: 5rem;
}
.c-orderItem-success {
  border-left: 0.25rem solid #58a347;
}
.c-orderItem-info {
  border-left: 0.25rem solid #7592cb;
}
.c-orderItem-error {
  border-left: 0.25rem solid #ea7b75;
}
.c-orderItem-warning {
  border-left: 0.25rem solid #e8a163;
}
.c-orderItem-title {
  display: inline-flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 0 1 50%;
  color: #333333;
}
.c-orderItem-title-main {
  font-size: 1rem;
  padding-bottom: 0.75rem;
  font-weight: 500;
  word-break: break-word;
}
.c-orderItem-extra {
  display: inline-flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: right;
  flex: 0 1 50%;
  color: #868686;
}
.c-orderItem-extra-main {
  padding-bottom: 1rem;
}
.c-orderItem-highLight {
  color: #ee764e;
}
